import OrderGoods from "@/components/OrderGoods";
import CouponListWindow from "@/components/CouponListWindow";
import AddressWindow from "@/components/AddressWindow";
import {
	postOrderConfirm,
	postOrderComputed,
	createOrder
} from "@/api/order";
import {
	mapGetters
} from "vuex";
import {
	weappPay
} from "@/libs/wechat";
import {
	isWeixin
} from "@/utils";

const NAME = "OrderSubmission",
	_isWeixin = isWeixin();
export default {
	name: NAME,
	components: {
		OrderGoods,
		CouponListWindow,
		AddressWindow
	},
	props: {},
	data: function() {
		return {
			offlinePayStatus: 2,
			from: "routine",
			// from: _isWeixin ? "weixin" : "weixinh5",
			deduction: true,
			isWeixin: _isWeixin,
			pinkId: 0,
			active: "weixin",
			showCoupon: false,
			showAddress: false,
			addressInfo: {},
			couponId: 0,
			orderGroupInfo: {
				priceGroup: {}
			},
			usableCoupon: {},
			addressLoaded: false,
			useIntegral: false,
			orderPrice: {
				payPrice: "计算中"
			},
			mark: "",
			system_store: {},
			shipping_type: 0,
			contacts: "",
			contactsTel: "",
			store_self_mention: 0
		};
	},
	computed: mapGetters(["userInfo"]),
	watch: {
		useIntegral() {
			this.computedPrice();
		},
		$yroute(n) {
			if (n.name === NAME) this.getCartInfo();
		},
		shipping_type() {
			this.computedPrice();
		}
	},
	mounted: function() {
		let that = this;
		that.getCartInfo();
		if (that.$yroute.query.pinkid !== undefined)
			that.pinkId = that.$yroute.query.pinkid;
	},
	methods: {
		addressType: function(index) {
			if (index && !this.system_store.id) {
				uni.showToast({
					title: "暂无门店信息，您无法选择到店自提！",
					icon: 'none',
					duration: 2000
				});
				return
			}
			this.shipping_type = index;
		},
		changeUseIntegral: function(e) {
			// this.computedPrice();
			this.useIntegral = e.mp.detail.value[0];
		},
		computedPrice() {
			let shipping_type = this.shipping_type;
			postOrderComputed(this.orderGroupInfo.orderKey, {
				addressId: this.addressInfo.id,
				useIntegral: this.useIntegral ? 1 : 0,
				couponId: this.usableCoupon.id || 0,
				shipping_type: parseInt(shipping_type) + 1
			}).then(res => {
				const data = res.data;
				if (data.status === "EXTEND_ORDER") {
					this.$yrouter.replace({
						path: "/pages/order/OrderDetails/index",
						query: {
							id: data.result.orderId
						}
					});
				} else {
					this.orderPrice = data.result;
				}
			});
		},
		getCartInfo() {
			const cartIds = this.$yroute.query.id;
			if (!cartIds) {
				uni.showToast({
					title: "参数有误",
					icon: 'none',
					duration: 2000
				});
				return this.$yrouter.back();
			}
			postOrderConfirm(cartIds)
				.then(res => {
					this.offlinePayStatus = res.data.offline_pay_status;
					this.orderGroupInfo = res.data;
					this.deduction = res.data.deduction;
					this.usableCoupon = res.data.usableCoupon || {};
					this.addressInfo = res.data.addressInfo || {};
					this.system_store = res.data.system_store || {};
					this.store_self_mention = res.data.store_self_mention;
					this.computedPrice();
				})
				.catch(() => {
					uni.showToast({
						title: "加载订单数据失败",
						icon: 'none',
						duration: 2000
					});
				});
		},
		addressTap: function() {
			this.showAddress = true;
			if (!this.addressLoaded) {
				this.addressLoaded = true;
				this.$refs.mychild.getAddressList();
			}
		},
		addressRedirect() {
			this.addressLoaded = false;
			this.showAddress = false;
		},
		couponTap: function() {
			this.showCoupon = true;
		},
		changeCoupon: function(coupon) {
			if (!coupon) {
				this.usableCoupon = {
					couponTitle: "不使用优惠券",
					id: 0
				};
			} else {
				this.usableCoupon = coupon;
			}
			this.computedPrice();
		},
		payItem: function(index) {
			this.active = index;
		},
		changeAddress(addressInfo) {
			this.addressInfo = addressInfo;
		},
		createOrder() {
			let shipping_type = this.shipping_type;
			if (!this.active) {
				uni.showToast({
					title: "请选择支付方式",
					icon: 'none',
					duration: 2000
				});
				return
			}
			if (!this.addressInfo.id && !this.shipping_type) {
				uni.showToast({
					title: "请选择收货地址",
					icon: 'none',
					duration: 2000
				});
				return
			}

			if (this.shipping_type) {
				if (
					(this.contacts === "" || this.contactsTel === "") &&
					this.shipping_type
				) {
					uni.showToast({
						title: "请填写联系人或联系人电话",
						icon: 'none',
						duration: 2000
					});
					return
				}

				if (!/^1(3|4|5|7|8|9|6)\d{9}$/.test(this.contactsTel)) {
					uni.showToast({
						title: "请填写正确的手机号",
						icon: 'none',
						duration: 2000
					});
					return
				}
				if (!/^[\u4e00-\u9fa5\w]{2,16}$/.test(this.contacts)) {
					uni.showToast({
						title: "请填写您的真实姓名",
						icon: 'none',
						duration: 2000
					});
					return
				}
			}

			uni.showLoading({
				title: "生成订单中"
			});
			createOrder(this.orderGroupInfo.orderKey, {
					realName: this.contacts,
					phone: this.contactsTel,
					addressId: this.addressInfo.id,
					useIntegral: this.useIntegral ? 1 : 0,
					couponId: this.usableCoupon.id || 0,
					payType: this.active,
					pinkId: this.pinkId,
					seckillId: this.orderGroupInfo.seckill_id,
					combinationId: this.orderGroupInfo.combination_id,
					bargainId: this.orderGroupInfo.bargain_id,
					from: this.from,
					mark: this.mark || "",
					shippingType: parseInt(shipping_type) + 1
				})
				.then(res => {
					uni.hideLoading();
					const data = res.data;
					switch (data.status) {
						case "ORDER_EXIST":
						case "EXTEND_ORDER":
						case "PAY_DEFICIENCY":
						case "PAY_ERROR":
							uni.showToast({
								title: res.msg,
								icon: 'none',
								duration: 2000
							});
							this.$yrouter.replace({
								path: "/pages/order/OrderDetails/index",
								query: {
									id: data.result.orderId
								}
							});
							break;
						case "SUCCESS":
							uni.showToast({
								title: res.msg,
								icon: "none",
								duration: 2000
							});
							this.$yrouter.replace({
								path: "/pages/order/OrderDetails/index",
								query: {
									id: data.result.orderId
								}
							});
							break;
						case "WECHAT_H5_PAY":
							this.$yrouter.replace({
								path: "/pages/order/OrderDetails/index",
								query: {
									id: data.result.orderId
								}
							});
							setTimeout(() => {
								// location.href = data.result.jsConfig.mweb_url;
							}, 100);
							break;
						case "WECHAT_PAY":
							weappPay(data.result.jsConfig).then(res => {
								this.$yrouter.replace({
									path: "/pages/order/OrderDetails/index",
									query: {
										id: data.result.orderId
									}
								});
							});

							// 下面为原先微信支付方式，
							// pay(data.result.jsConfig).finally(() => {
							//   this.$yrouter.replace({
							//     path: "/pages/order/OrderDetails/index" ,query: { id: data.result.orderId}
							//   });
							// });
					}
				})
				.catch(err => {
					uni.hideLoading();
					uni.showToast({
						title: err.response.data.msg || "创建订单失败",
						icon: 'none',
						duration: 2000
					});
				});
		}
	}
};
